import React from "react";
import faq1 from "../images/FAQ1.svg";
import faq2 from "../images/FAQ2.svg";
import faq3 from "../images/FAQ3.svg";

export default function FAQ() {
  return (
    <div id="faqs">
      <div>
        <section id="adjust">
          <div className="faq">
            <div className="faq-left-desktop">
              <div className="faq-left-image-desktop">
                <img src={faq1} alt="illustration-woman-online-desktop" />
              </div>
              <img src={faq2} alt="illustration-box-desktop" />
            </div>
            <div className="faq-left-mobile">
              <div className="faq-left-image-mobile">
                <img src={faq3} alt="illustration-woman-online-mobile" />
              </div>
            </div>
            <div className="faq-right">
              <div className="faq-title">FAQ</div>
              <div className="faq-body">
                <details>
                  <summary>What is Aws Cloud Club ?</summary>
                  <p>
                    The AWS Cloud Club at IIPS, DAVV, Indore is the first cloud
                    club in Madhya Pradesh, providing students with
                    opportunities to learn and excel in AWS cloud computing
                    through events, workshops, and exclusive benefits.
                  </p>
                </details>
                <details>
                  <summary>
                    What are the benefits of joining Aws Cloud Club ?
                  </summary>
                  <p>
                    <ul id="list-faq">
                      <li>WORKSHOPS AND MEETING SESSIONS</li>
                      <li>SKILL DEVELOPMENT</li>
                      <li>NETWORKING</li>
                      <li>EXCLUSIVE RESOURCES</li>
                      <li>CAREER OPPRTUNITIES</li>
                    </ul>
                  </p>
                </details>
                <details>
                  <summary>What type of events do we conduct?</summary>
                  <p>
                    The AWS Cloud Club conducts workshops, seminars, hackathons,
                    webinars, and networking events to provide hands-on
                    learning, industry insights, and networking opportunities
                    for members to excel in cloud computing.
                  </p>
                </details>
                <details>
                  <summary>
                    How AWS cloud club help me in my career or studies ?
                  </summary>
                  <p>
                    AWS cloud club can help you in your career and studies by
                    providing opportunities to learn new skills, build a
                    network, gain hands-on experience, build your resume, and
                    prepare for certification exams.
                  </p>
                </details>
                <details>
                  <summary>
                    What kind of skills will I learn in AWS cloud club?
                  </summary>
                  <p>
                    In AWS cloud club, you will learn skills related to cloud
                    computing, such as how to use AWS services like EC2, S3, and
                    RDS, how to deploy and manage applications on AWS, and how
                    to use AWS tools like CloudFormation and Elastic Beanstalk.
                    You may also learn about cloud security, networking, and
                    automation.
                  </p>
                </details>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
}
